@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "RobotoCondensed";
  font-display: swap;
  src: url("../../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Chalkcyrillicfreehand";
  font-display: swap;
  src: url("../../fonts/Chalkcyrillicfreehand.woff2") format("woff2"), url("../../fonts/Chalkcyrillicfreehand.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-family: "RobotoCondensed";
  font-size: 16px;
  line-height: 24px;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #2b2a28;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 200px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.promo .promo__container {
  padding-top: 40px;
}

.promo__preface {
  font-size: 18px;
  line-height: 22px;
  font-weight: 600;
  color: #e8f0c1;
  letter-spacing: 0.04em;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  width: 30%;
  margin: 0 auto;
  text-align: center;
}
@media (max-width: 1279px) {
  .promo__preface {
    width: 40%;
  }
}
@media (max-width: 1023px) {
  .promo__preface {
    width: 50%;
  }
}
@media (max-width: 767px) {
  .promo__preface {
    font-size: 14px;
    line-height: 22px;
    font-weight: 500;
    color: #e8f0c1;
    width: 100%;
  }
}

.promo__title {
  font-size: 135px;
  line-height: 145px;
  font-weight: 500;
  color: #ff1c1c;
  font-family: "Chalkcyrillicfreehand", "Arial", sans-serif;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
}
@media (max-width: 767px) {
  .promo__title {
    font-size: 17vw;
    line-height: 122%;
    font-weight: 500;
    color: #ff1c1c;
  }
}

.promo__logo {
  margin: 0 auto;
}
@media (max-width: 767px) {
  .promo__logo {
    transition: 0.5s ease-in-out;
    width: 70%;
  }
}
@media (max-width: 554px) {
  .promo__logo {
    transition: 0.5s ease-in-out;
    width: 100%;
  }
}

.fighters .fighters__container {
  padding-top: 50px;
  padding-bottom: 50px;
}
@media (max-width: 767px) {
  .fighters .fighters__container {
    padding-top: 35px;
    padding-bottom: 35px;
  }
}

.fighters__subtitle {
  font-size: 28px;
  line-height: 32px;
  font-weight: 700;
  color: #a184a5;
  text-align: center;
}

.fighters__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 1023px) {
  .fighters__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 554px) {
  .fighters__list {
    grid-template-columns: 1fr;
  }
}

.fighters__item {
  width: 100%;
  margin-bottom: 20px;
  min-height: 70px;
  background: linear-gradient(90deg, rgba(96, 128, 83, 0.75) 0%, rgba(0, 0, 0, 0) 75%);
  display: flex;
  align-items: center;
}
@media (max-width: 767px) {
  .fighters__item {
    background: linear-gradient(90deg, rgba(96, 128, 83, 0.75) 0%, rgba(0, 0, 0, 0) 100%);
    min-height: 50px;
  }
}

.fighters__text {
  font-size: 18px;
  line-height: 22px;
  font-weight: 500;
  color: #ffffff;
  text-transform: uppercase;
  margin: 0;
  padding-left: 20px;
}
@media (max-width: 767px) {
  .fighters__text {
    font-size: 14px;
    line-height: 18px;
    font-weight: 400;
    color: #ffffff;
  }
}

.join-squad__title {
  font-size: 28px;
  line-height: 32px;
  font-weight: 600;
  color: #a184a5;
  text-transform: uppercase;
  text-align: center;
}
@media (max-width: 767px) {
  .join-squad__title {
    font-size: 22px;
    line-height: 28px;
    font-weight: 600;
    color: #a184a5;
  }
}

.join-squad__text {
  font-size: 18px;
  line-height: 32px;
  font-weight: 500;
  color: #a184a5;
  text-align: center;
  width: 60%;
  margin: 0 auto 30px;
}
@media (max-width: 554px) {
  .join-squad__text {
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    color: #a184a5;
    width: 100%;
  }
}

.join-squad__list {
  list-style-type: none;
  width: 500px;
  margin: 0 auto;
  padding: 0;
}
@media (max-width: 767px) {
  .join-squad__list {
    width: 100%;
  }
}

.join-squad__item {
  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
  color: #ff1c1c;
  text-align: center;
  text-transform: uppercase;
  padding: 10px 20px;
  border-radius: 10px;
  background-color: rgba(22, 22, 22, 0.3098039216);
  box-shadow: 0px 0px 18px -10px #ffffff;
}
.join-squad__item:not(:last-child) {
  margin-bottom: 10px;
}
@media (max-width: 767px) {
  .join-squad__item {
    font-size: 16px;
    line-height: 22px;
    font-weight: 600;
    color: #ff1c1c;
  }
}
@media (max-width: 554px) {
  .join-squad__item {
    font-size: 14px;
    line-height: 18px;
    font-weight: 500;
    color: #ff1c1c;
  }
}

.go-front .go-front__container {
  padding: 50px;
}
@media (max-width: 767px) {
  .go-front .go-front__container {
    padding: 30px 10px;
  }
}

.go-front__wrapper-content {
  background-color: #393636;
  width: 70%;
  margin: 0 auto;
  padding: 30px;
  -webkit-box-shadow: -5px -5px 5px -5px rgba(144, 144, 144, 0.6);
  -moz-box-shadow: -5px -5px 5px -5px rgba(34, 60, 80, 0.6);
  box-shadow: -5px -5px 5px -5px rgba(255, 255, 255, 0.6);
}
@media (max-width: 767px) {
  .go-front__wrapper-content {
    width: 100%;
    padding: 10px 5px;
    padding-bottom: 30px;
  }
}

.go-front__text {
  font-size: 18px;
  line-height: 32px;
  font-weight: 500;
  color: #ffffff;
  letter-spacing: 0.03em;
  text-align: center;
  width: 100%;
  margin: 0 auto 50px;
}
@media (max-width: 767px) {
  .go-front__text {
    font-size: 16px;
    line-height: 22px;
    font-weight: 500;
    color: #ffffff;
    width: 100%;
    letter-spacing: 0.05em;
  }
}
.go-front__text--go {
  width: 80%;
  text-align: center;
  color: #ffffff;
}

.go-front__contacts {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 50px;
}
@media (max-width: 767px) {
  .go-front__contacts {
    flex-direction: column;
    margin-bottom: 20px;
  }
}

.go-front__link-phone {
  font-size: 20px;
  line-height: 28px;
  font-weight: 500;
  color: #ffffff;
  letter-spacing: 0.05em;
  display: block;
  margin-bottom: 15px;
}
@media (max-width: 767px) {
  .go-front__link-phone {
    font-size: 16px;
    line-height: 20px;
    font-weight: 500;
    color: #ffffff;
    letter-spacing: 0.03em;
  }
}

.go-front__link-phone span {
  font-size: 18px;
  line-height: 24px;
  font-weight: 500;
  color: #ff5454;
}

.contract__title {
  font-size: 28px;
  line-height: 32px;
  font-weight: 500;
  color: #a184a5;
  text-transform: uppercase;
  text-align: center;
}
@media (max-width: 767px) {
  .contract__title {
    font-size: 22px;
    line-height: 28px;
    font-weight: 500;
    color: #a184a5;
  }
}

.contact__content-wrapper {
  padding: 20px;
