  }
}

.contract__list {
  margin: 0 auto;
  padding: 0;
  width: 80%;
}

.contract__item {
  font-size: 18px;
  line-height: 32px;
  font-weight: 500;
  color: #ffffff;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .contract__item {
    font-size: 16px;
    line-height: 22px;
    font-weight: 500;
    color: #ffffff;
  }
}

.contract__text {
  font-size: 20px;
  line-height: 32px;
  font-weight: 500;
  color: #ea3e3e;
  margin: 0;
  margin-bottom: 20px;
  text-align: center;
}
@media (max-width: 767px) {
  .contract__text {
    font-size: 16px;
    line-height: 22px;
    font-weight: 500;
    color: #ea3e3e;
  }
}

.contract__text a {
  font-size: 22px;
  line-height: 32px;
  font-weight: 500;
  color: #eac83e;
  margin-bottom: 20px;
  text-align: center;
}
@media (max-width: 767px) {
  .contract__text a {
    font-size: 16px;
    line-height: 22px;
    font-weight: 500;
    color: #eac83e;
  }
}